.kakao {
  max-width: 400px;
}
.container {
  max-width: 400px;
  margin: 0 auto;
  padding: 0px;
}
.id {
  width: 400px;
  height: 30px;
  font-size: 25px;
  margin-bottom: 5px;
}

.hello {
  font-size: 32px;
  color: black;
  filter: contrast(80%);
}
.btn {
  font-family: "Helvetica", "Arial", sans-serif;
  float: right;
  font-size: 12px;
  padding: 7px;
  cursor: pointer;
  background-color: darkgrey;
  border: 0;
  padding: 8px 20px;

  text-transform: uppercase;
  cursor: pointer;
}
.outbtn {
  background-color: rgba(255, 0, 0, 0);
  border-style: none;
}
.arrow {
  width: 25px;
  height: 16px;
  background-color: #f9e000;
}
